@import "~ionic-angular/scss/mixins";
.page-login {
    background: transparent;
    background-size: cover;
    overflow: hidden;

    .video {
        position: absolute;
        top: 50%; 
        left: 50%;
        -webkit-transform: translateX(-50%) translateY(-50%);
        transform: translateX(-50%) translateY(-50%);
        min-width: 100%; 
        min-height: 100%; 
        width: auto; 
        height: auto;
        z-index: -1000; 
        overflow: hidden;

    }

    .container {
        height: 100%;
        position: relative;
        padding-top: 50%;
    }

    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.2;
        background-color: purple;
    }

    .login-buttons {    
        .button {
            color: white;

            i {
                opacity: 0.6;
                margin-right: 15px;
            }
        }
    }

    .button-facebook {
        background: #3b5998;

    }

    .button-google-plus {
        background: #d34836;
    }
}


