@import "~ionic-angular/scss/mixins";
.page-home {

    .bar-home {
        background: #a20dd8;
        
        .title{
            color: white;

        }
    }

    .pesquisar{
        padding-top: 10%;  
    }

    .lista-menu{
        padding-top: 16%;
    }


    .container{
        background: #a20dd8;
    }

    .estabelecimento-info{
        padding-left: 10px;
        
    }

    .logo-estabelecimento{
        max-height: 130px;
    }

    .local-icon-scroller {
    height: 87px;
    -webkit-overflow-scrolling: touch;
    padding: 15px 15px 12px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    list-style: none;
    }

    .local-icon {
        display: inline-block;
        vertical-align: top;
        height: 50px;
        white-space: normal;
        margin: 0 10px
    }

}


